.about {
  position: relative;
  z-index: 10;
  padding: 80px;
  -webkit-box-shadow: 0 -5px 15px rgba(0,0,0,.3), 0 5px 15px rgba(0,0,0,.3);
          box-shadow: 0 -5px 15px rgba(0,0,0,.3), 0 5px 15px rgba(0,0,0,.3);
  text-align: center;
  background-color: #fff;
}

.avatar {
  display: inline-block;
  width: 120px;
  height: 120px;
  margin: 10px;
  border-radius: 1000px;
}

.about-text {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  text-align: left;
}

@media (max-width: 800px) {
  .about {
    padding: 30px;
  }

  .about-text {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .avatar {
    width: 200px;
    height: 200px;
  }
}



